@import '../../design-tokens/scss-variables';

.social-links-list {
	display: flex;

	.social-links-item {
		&:not(:last-child) {
			margin-right: $spacer-6;
		}
	}

	.social-links-link {
		color: $color-dark-grey;
		display: flex;
		padding: $spacer-1;
		transition: color 0.3s ease;

		&:hover {
			color: $color-white;
		}

		&:focus {
			outline: none;
		}

		&:focus-visible {
			border-radius: 4px;
			box-shadow: 0 0 0 0.4rem rgba($color-white, 0.4);
		}
	}
}

.navbar-menu-social {
	.social-links-link {
		font-size: $icon-size-6;
	}
}

.footer-social {
	margin-bottom: $spacer-5 * 2;

	@media (min-width: 992px) {
		margin-bottom: $spacer-8;
	}

	.social-links-link {
		font-size: $icon-size-7;
	}
}
